import { game } from '../base/globalVar';
/**
 * 
 * @param {Array} arr ---> 
 * @param {object} graphics 
 * @param {number} currentIndex  
 * @param {number} time 
 * @param {function} callback 
 */
function drawLine(arr, graphics, currentIndex, config, callback) {

    let {
        scale,
        px,
        py } = config

    if (currentIndex <= arr.length - 1) {
        let item = arr[currentIndex]

        if (item) {
            if (currentIndex === 0) {
                graphics.beginPath();
                graphics.moveTo(transformXY(item.x, px, scale), transformXY(item.y, py, scale));
            } else {
                graphics.lineTo(transformXY(item.x, px, scale), transformXY(item.y, py, scale));
                graphics.stroke();
                if (currentIndex === arr.length - 1) {

                    callback && callback();
                }

            }
        }

        drawLine(arr, graphics, currentIndex + 1, config, callback);

    }

}

/**
 *  drawLine 
 * @param {Array} dataArr 
 * @param {number} currentIndex 
 * @param {object} bmd BitmapData
 * @param {number} time1 
 * @param {number} time2 
 */
function drawTask(dataArr, currentIndex, bmd, config) {
    if (currentIndex <= dataArr.length) {


        if (dataArr && dataArr[currentIndex]) {
            drawLine(dataArr[currentIndex], mypen(config.color, bmd, config.width), 0, config, function () {
                drawTask(dataArr, currentIndex + 1, bmd, config);

            });
        }
    }

}

/**
 * 
 * @param {number} x 
 * @param {number} px 
 * @param {number} scale 
 */
function transformXY(x, px, scale) {
    return (x + px) * scale;
}

/**
 * 
 * @param {string} color 
 * @param {object} bmd BitmapData
 */
function mypen(color, bmd, width) {

    var ctx = bmd.ctx;

    ctx.lineCap = 'round';
    ctx.lineWidth = width ? width : 60;
    ctx.strokeStyle = color;


    return ctx;
}

export {
    drawTask
};


